:global * {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

:global ::selection {
  background-color: #0c0c0c;
  color: white; }

:global body, :global html {
  position: absolute;
  margin: 0;
  padding: 0;
  min-height: 100%;
  width: 100%;
  display: block;
  font-size: 16px;
  font-family: -apple-system, system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif; }

:global #__next {
  position: absolute;
  margin: 0;
  padding: 0;
  min-height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  color: rgba(255, 255, 255, 0.86);
  line-height: 1.3rem;
  overflow-x: hidden;
  height: 100%;
  width: 100vw;
  overflow: auto;
  scroll-behavior: smooth; }

:global a {
  color: inherit;
  text-decoration: none; }

:global a:hover, :global a:focus, :global a:active {
  outline: none; }
