@keyframes fade-in {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes flicker-by {
  0% {
    background-position-y: 0.0px, center; }
  10% {
    background-position-y: 0.2px, center; }
  30% {
    background-position-y: 0.95px, center; }
  40% {
    background-position-y: 2.5px, center; }
  50% {
    background-position-y: 0.8px, center; }
  90% {
    background-position-y: 1.8px, center; } }

@keyframes swoosh-bg {
  0% {
    background-position-y: 40%; }
  33% {
    background-position-y: 70%; }
  66% {
    background-position-y: 100%; }
  100% {
    background-position-y: 140%; } }

@keyframes flicker-y-1 {
  0% {
    transform: translatey(0px); }
  9% {
    transform: translatey(0px); }
  10% {
    transform: translatey(1.2px); }
  19% {
    transform: translatey(1.2px); }
  30% {
    transform: translatey(0.95px); }
  39% {
    transform: translatey(0.95px); }
  40% {
    transform: translatey(1.2px); }
  49% {
    transform: translatey(1.2px); }
  50% {
    transform: translatey(0.8px); }
  70% {
    transform: translatey(0.8px); }
  90% {
    transform: translatey(1.4px); } }

@keyframes flicker-y-2 {
  0% {
    transform: translatey(0px); }
  9% {
    transform: translatey(0px); }
  10% {
    transform: translatey(-1.2px); }
  19% {
    transform: translatey(-1.2px); }
  30% {
    transform: translatey(-0.95px); }
  39% {
    transform: translatey(-0.95px); }
  40% {
    transform: translatey(-1.2px); }
  49% {
    transform: translatey(-1.2px); }
  50% {
    transform: translatey(-0.8px); }
  70% {
    transform: translatey(-0.8px); }
  90% {
    transform: translatey(-1.4px); } }

@keyframes flicker-bx-1 {
  0% {
    background-position-x: 0.0px, center; }
  10% {
    background-position-x: 0.1px, center; }
  30% {
    background-position-x: 0.5px, center; }
  40% {
    background-position-x: 2.5px, center; }
  60% {
    background-position-x: 0.6px, center; }
  100% {
    background-position-x: 1.8px, center; } }

@keyframes flicker-bx-2 {
  0% {
    background-position-x: -0.0px, center; }
  10% {
    background-position-x: -0.1px, center; }
  30% {
    background-position-x: -0.5px, center; }
  40% {
    background-position-x: -2.5px, center; }
  60% {
    background-position-x: -0.6px, center; }
  100% {
    background-position-x: -1.8px, center; } }

.header {
  position: relative;
  width: 100%;
  height: auto;
  top: 2px;
  display: flex;
  place-content: space-around;
  place-items: center;
  flex-direction: column;
  background: #0c0c0c;
  z-index: 3200;
  box-shadow: 0 0 5px 0 black; }
  .header ::selection {
    background-color: #0c0c0c;
    color: white; }

.anibar {
  z-index: 3000;
  height: 2px;
  top: 0;
  width: 100%;
  position: fixed;
  opacity: 0.8; }
  .anibar::after {
    content: "";
    height: 2px;
    width: 100%;
    position: absolute;
    background-size: 100%;
    background-size: cover;
    background-position: center 40%;
    transition: all 0.5s ease-in-out;
    background-image: linear-gradient(to right, #0c0c0c 5%, rgba(12, 12, 12, 0.2) 15%, rgba(12, 12, 12, 0.2) 55%, #0c0c0c 95%), url("../images/bg1.jpg");
    animation: swoosh-bg infinite 20s linear; }
    @media (max-width: 999px) {
      .anibar::after {
        background-size: 100%;
        background-image: linear-gradient(to right, #0c0c0c 3%, rgba(12, 12, 12, 0.2) 15%, rgba(12, 12, 12, 0.2) 50%, #0c0c0c 97%), url("../images/bg1.jpg"); } }

.bar {
  overflow: hidden;
  position: relative;
  background: #0c0c0c;
  height: 100px; }

.barbg {
  filter: blur(5px);
  content: "";
  background: #0c0c0c;
  opacity: 1;
  width: 120%;
  height: 100%;
  position: absolute;
  background-size: 100%;
  background-attachment: fixed;
  background-size: cover;
  background-position: center 50%;
  transition: all 0.5s ease-in-out;
  background-image: url("../images/bg2.jpg");
  z-index: 1100; }
  @media (max-width: 999px) {
    .barbg {
      background-image: url("../images/bg2.jpg"); } }

.title {
  position: relative; }
  .title > span {
    font-family: Inconsolata;
    position: relative;
    font-size: 1.6rem;
    font-weight: 100;
    height: 3.2rem;
    margin: 1rem;
    padding: 0;
    color: white;
    width: 30vw;
    max-width: 300px;
    min-width: 220px;
    text-align: center;
    display: flex;
    place-items: center;
    place-content: center;
    letter-spacing: 0.05rem;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.1) inset; }
    @media (max-width: 999px) {
      .title > span {
        font-size: 1.5rem; } }

