@keyframes fade-in {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes flicker-by {
  0% {
    background-position-y: 0.0px, center; }
  10% {
    background-position-y: 0.2px, center; }
  30% {
    background-position-y: 0.95px, center; }
  40% {
    background-position-y: 2.5px, center; }
  50% {
    background-position-y: 0.8px, center; }
  90% {
    background-position-y: 1.8px, center; } }

@keyframes swoosh-bg {
  0% {
    background-position-y: 40%; }
  33% {
    background-position-y: 70%; }
  66% {
    background-position-y: 100%; }
  100% {
    background-position-y: 140%; } }

@keyframes flicker-y-1 {
  0% {
    transform: translatey(0px); }
  9% {
    transform: translatey(0px); }
  10% {
    transform: translatey(1.2px); }
  19% {
    transform: translatey(1.2px); }
  30% {
    transform: translatey(0.95px); }
  39% {
    transform: translatey(0.95px); }
  40% {
    transform: translatey(1.2px); }
  49% {
    transform: translatey(1.2px); }
  50% {
    transform: translatey(0.8px); }
  70% {
    transform: translatey(0.8px); }
  90% {
    transform: translatey(1.4px); } }

@keyframes flicker-y-2 {
  0% {
    transform: translatey(0px); }
  9% {
    transform: translatey(0px); }
  10% {
    transform: translatey(-1.2px); }
  19% {
    transform: translatey(-1.2px); }
  30% {
    transform: translatey(-0.95px); }
  39% {
    transform: translatey(-0.95px); }
  40% {
    transform: translatey(-1.2px); }
  49% {
    transform: translatey(-1.2px); }
  50% {
    transform: translatey(-0.8px); }
  70% {
    transform: translatey(-0.8px); }
  90% {
    transform: translatey(-1.4px); } }

@keyframes flicker-bx-1 {
  0% {
    background-position-x: 0.0px, center; }
  10% {
    background-position-x: 0.1px, center; }
  30% {
    background-position-x: 0.5px, center; }
  40% {
    background-position-x: 2.5px, center; }
  60% {
    background-position-x: 0.6px, center; }
  100% {
    background-position-x: 1.8px, center; } }

@keyframes flicker-bx-2 {
  0% {
    background-position-x: -0.0px, center; }
  10% {
    background-position-x: -0.1px, center; }
  30% {
    background-position-x: -0.5px, center; }
  40% {
    background-position-x: -2.5px, center; }
  60% {
    background-position-x: -0.6px, center; }
  100% {
    background-position-x: -1.8px, center; } }

.header {
  position: relative;
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: #0c0c0c;
  z-index: 3000;
  box-shadow: 0 0 5px 0 black; }
  .header ::selection {
    background-color: #0c0c0c;
    color: white; }
  .header::before, .header::after {
    content: "";
    height: 1px;
    background: #0c0c0c;
    opacity: 0.4;
    width: 100%;
    position: absolute;
    background-size: 100%;
    background-attachment: fixed;
    background-size: cover;
    background-position: center 40%;
    transition: all 0.5s ease-in-out;
    background-image: linear-gradient(to right, #0c0c0c 5%, rgba(12, 12, 12, 0.2) 15%, rgba(12, 12, 12, 0.2) 55%, #0c0c0c 95%), url("../images/bg1.jpg"); }
    @media (max-width: 999px) {
      .header::before, .header::after {
        background-size: 100%;
        background-image: linear-gradient(to right, #0c0c0c 3%, rgba(12, 12, 12, 0.2) 15%, rgba(12, 12, 12, 0.2) 50%, #0c0c0c 97%), url("../images/bg1.jpg"); } }
  .header::before {
    animation: swoosh-bg infinite 20s linear; }
  .header::after {
    animation: swoosh-bg infinite 20s linear; }

